/* Scroll Buttons Styling */
.app { font-family: 'Edu AU VIC WA NT Pre';}

.scroll-buttons {
  position: fixed;
  top: 50%;
  right: 1rem;
  transform: translateY(-50%);
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
  z-index: 1000;
}

.scroll-button {
  width: 40px;
  height: 40px;
  border: none;
  border-radius: 50%;
  background-color: #333;
  color: #fff;
  font-size: 20px;
  cursor: pointer;
  transition: transform 0.3s, background-color 0.3s;
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.2);
}

.scroll-button:disabled {
  background-color: #555;
  cursor: not-allowed;
}

.scroll-button:hover:enabled {
  transform: scale(1.1);
  background-color: #f50e59;
}

.scroll-button:active:enabled {
  transform: scale(0.95);
}

/* Responsive Adjustments */
@media (max-width: 480px) {
  .scroll-button {
      width: 30px;
      height: 30px;
      font-size: 16px;
  }
}
