@import url('https://fonts.googleapis.com/css2?family=Edu+AU+VIC+WA+NT+Pre:wght@400..700&family=Kalam:wght@300;400;700&family=Outfit:wght@100..900&display=swap');

.current-work {
    padding: 2rem;
    font-family: 'Edu AU VIC WA NT Pre', sans-serif;
    /* background: linear-gradient(to bottom right, #ffe4e1, #fff7e9);  */
}

 h2 {
    font-size: 2rem;
    color: #f50e59;
    text-align: center;
    margin-bottom: 1rem;
}

.current-work-container {
    display: flex;
    flex-direction: column;
    gap: 2rem;
    align-items: center;
}

/* Caption/Text Styles */
.caption {
    line-height: 1.8;
    font-family: 'Edu AU VIC WA NT Pre';
    font-weight: 500;
    color: #333;
    text-align: center;
    margin-bottom: 1rem;
}

.caption p {
    font-size: 1rem;
    line-height: 1.6;
    margin-bottom: 1rem;
}

.rRlogo img {
    width: 200px;
    border-radius: 8px;
}

/* Grid for Videos */
.grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr); /* Default for larger screens */
    gap: 1rem;
    width: 100%;
}

/* Video Wrapper */
.video-wrapper {
    weight: 80%;
    position: relative;
    cursor: pointer;
    transition: transform 0.3s ease;
}

.video-wrapper:hover {
    transform: scale(1.02); /* Slight zoom on hover */
}

.video-wrapper video {
    width: 100%;
    border-radius: 8px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

/* Play Icon */
.play-icon {
    position: absolute;
    top: 50%;
    left: 50%;
    font-size: 2rem;
    color: white;
    background: rgba(0, 0, 0, 0.6); /* Semi-transparent background */
    border-radius: 50%;
    width: 3rem;
    height: 3rem;
    display: flex;
    align-items: center;
    justify-content: center;
    pointer-events: none;
    opacity: 1;
    transition: opacity 0.3s ease;
}

/*.video-wrapper:hover .play-icon,*/

.video-wrapper.playing .play-icon {
    opacity: 0;
}

/* Overlay Styles */
.overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background: rgba(0, 0, 0, 0.8); /* Semi-transparent dark background */
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 1000;
}

.overlay .video-container {
    position: relative;
    width: 50%;
    max-width: 280px;
    border-radius: 10px;
    overflow: hidden;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.3);
}

.overlay-video {
    width: 100%;
    height: auto;
    border-radius: 10px;
}

/* Responsive Adjustments for Small Screens */
@media (max-width: 994px) {
    .rRlogo img {
        width: 147px;
    }

    h2 { font-size: 1.5rem;}
}

@media (max-width: 768px) {
  
    .caption {
        margin-top: 1rem;
    }

    .grid {
        grid-template-columns: 1fr; /* Single column for videos */
        gap: 0.5rem;
    }

    .video-wrapper {
        width: 100%;
        max-width: 400px;
        margin: 0 auto;
    }

    .video-wrapper video {
        width: 100%;
        border-radius: 8px;
    }

    .play-icon {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        font-size: 1.8rem;
        width: 2.5rem;
        height: 2.5rem;
    }
}

@media (max-width: 740px) {
    .caption p { font-size: 14px; }
    h2 { font-size: 1rem;}
}

@media (max-width: 480px) {
 
    .caption p {
        font-size: 0.8rem;
    }

    .play-icon {
        font-size: 1.5rem;
        width: 2rem;
        height: 2rem;
    }
}

@media (max-width: 392px) {
    .overlay {
        width: 100%;
        height: 100%;
    }
}

@media (max-width: 380px) { h2 {font-size: 14px;} }
