.image-grid {
    text-align: center;
    padding: 1rem;
}

.image-grid .grid {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    gap: 0.5rem;
    margin: 1rem 0;
}

.image-grid img {
    width: 100%;
    border-radius: 8px;
}

.image-grid .buttons {
    margin-top: 1rem;
}

.image-grid button {
    padding: 0.5rem 1rem;
    background-color: #333;
    color: #fff;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

.image-grid button:hover {
    background-color: #fc037b;
}

.footer {
    margin-top: 2rem;
    padding: 1rem;
    /* background-color: #222; */
    background: url('../../inegaImg/footerCover.jpeg')no-repeat center center/cover;
    color: #fff;
    text-align: center;
    border-radius: 8px;
}

.footer a {
    color: #f50e59;
    text-decoration: none;
}

.footer .social-media {
    margin-top: 1rem;
}

.footer .social-media img {
    width: 24px;
    vertical-align: middle;
    margin-right: 0.5rem;
}

/* Responsive Design */
@media (max-width: 994px) {
    .image-grid .grid { grid-template-columns: repeat(3, 1fr);}
}

@media (max-width: 740px) {
    .image-grid .grid { grid-template-columns: repeat(2, 1fr);}

    p { font-size: 16px; }

    .footer .social-media { font-size: 16px; }

    .footer .social-media img { width: 20px; }

}

@media (max-width: 480px) {
    .footer { padding: 1rem 0.5rem; }

    p { font-size: 12px; }

    .footer .social-media { font-size: 12px; }

    .footer .social-media img { width: 15px; }
}
