.intro {
    position: relative;
    text-align: left;
    padding: 2rem;
    height: 500px;
    background: url('../../inegaImg/cover2.jpeg') no-repeat center center/cover;
    color: white;
    display: flex;
    align-items: center;
    justify-content: flex-start;
}

.intro::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.4);
    z-index: 1;
}

.text-content {
    position: relative;
    z-index: 2;
    max-width: 600px;
}

.name {
    font-size: 3rem;
    margin: 0;
    font-weight: bold;
    text-shadow: 2px 2px 5px rgba(0, 0, 0, 0.5);
}

.subtitle {
    font-size: 1.2rem;
    margin: 0.5rem 0 1.5rem;
    font-style: italic;
    color: transparent; /* Make text transparent for the glitter effect */
    background: linear-gradient(90deg, #e80193, #8d0461, #fa4a73);
   /* background: linear-gradient(90deg, #ffd700, #ffea8a, #ffd700); */ /* Gold gradient */
    background-size: 200%;
    background-clip: text;
    -webkit-background-clip: text;
    position: relative;
    display: inline-block;
    animation: glitter-move 3s linear infinite; /* Gold gradient shimmer */
}

.subtitle::after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: url('https://upload.wikimedia.org/wikipedia/commons/3/3f/Transparent_noise_texture.png'); /* Glitter texture */
    background-size: cover;
    opacity: 0.3; /* Adjust opacity for subtle glitter */
    animation: glitter-sparkle 1.5s infinite ease-in-out alternate;
    -webkit-background-clip: text;
    background-clip: text;
}

@keyframes glitter-move {
    0% {
        background-position: 0% 0%;
    }
    100% {
        background-position: 100% 0%;
    }
}

@keyframes glitter-sparkle {
    0% {
        opacity: 0.2;
    }
    100% {
        opacity: 0.5;
    }
}


.measurements {
    list-style: none;
    padding: 0;
    margin: 0;
    font-size: 0.9rem;
    line-height: 1.6;
    color: #e8e8e8;
}

@media (max-width: 678px) {
    .intro { height: 400px;}

    .name {
        font-size: 2rem;
    }

    .subtitle {
        font-size: 1rem;
        line-height: 1.4;
        white-space: normal; /* Allows text to wrap */
        word-break: break-word; /* Ensures long words break to fit */
    }

    .measurements {
        font-size: 0.8rem;
    }
}

@media (max-width:792px) {
    .subtitle {
        font-size: 1rem;
    }
}

@media (max-width:598px) {
    .intro { height: 300px;}

    .subtitle {
        font-size: 10px;
    }
}

@media (max-width:483px) {
    .intro { height: 133px;}

    .measurements { font-size: 9px;}
}

@media (max-width:380px) { 
    .name { font-size: 1rem;} 

    .subtitle { font-size: 5px;}

    .measurements {font-size: 5px !important;}
}